import React, { useState } from "react";
import BackendSingleField from "./BackendSingleField";

function randID()
{ 
    return Math.random().toString(36).substr(2, 8)
}

function Backend({ data })
{
    const [admindata, setAdmindata] = useState(Object.keys(data).map((key) => (
        <BackendSingleField label={key} value={data[key]} id={`row-${key}-${randID()}`} key={randID() } />
    )))

    function handleSubmission(e)
    { 
        e.preventDefualt()
        console.log('submitted')
    }
    
    return (
        <div className="backend">
            <div className="backend-inner">
                <form onSubmit={handleSubmission}>
                    {admindata}
                    <button type="submit">Submit</button>
                </form>
            </div>
        </div>
    )
}

export default Backend
