import { useState, useEffect } from 'react'

function randID()
{ 
    return Math.random().toString(36).substr(2, 8)
}

function BackendSingleField({ label, value, id})
{
    const [_label, setLabel] = useState(label)
    const [_value, setValue] = useState(value)
    const [multiValues, setMultiValues] = useState("Error - somthing wrong with the data!")

    useEffect(() =>
    {
        const removeField = (id) =>
        { 
            const newadmindata = multiValues.filter(function (row) {
                return row.id !== id;
            });
            console.log(newadmindata)
        }

        if (_value.constructor.name === "Array")
        {
            const arrType = (
                <div className={_label + ' row multiple'} id={id}>
                    <label>{_label}</label>
                    {_value.map((val) => (
                        <BackendSingleField label={val.id} value={val} id={"row-" + val.id + "-" + randID() } key={randID()} />
                    ))}
                </div>
            );
            setMultiValues(arrType)
        } else if (_value.constructor.name === "Object")
        {
            const objType = (
                <div className={_label + ' row object'} id={id} >
                    {Object.keys(_value).map((val) => (
                        <BackendSingleField label={val} value={_value[val]} id={"row-" + val + "-" + randID() } key={randID()} />
                    ))}
                    <button onClick={(id) => removeField}>Delete</button>
                </div>
            )
        setMultiValues(objType)
    } else
    {
        setMultiValues(
            <div className="row" id={id}>
                <label>{ _label }</label>
                <input type="text" name={_label + "-" + randID()} id={_label + "-" + randID()} value={_value}  onChange={(e) => setValue(e.target.value)} />
            </div>
            )
        }
    },[_label, _value])

    return multiValues
}

export default BackendSingleField
